/* ==========================================================================
   mixins
   ========================================================================== */
/* eof mixins */
@media (max-width: 991px) {
  .forWho {
    padding-bottom: 30px; } }

.forWho-list {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 0 -19px 0 -12px; }
  @media (max-width: 991px) {
    .forWho-list {
      margin-left: 0;
      margin-right: 0; } }
  .forWho-list__single {
    width: 50%;
    padding: 0 19px 40px 12px; }
    @media (max-width: 991px) {
      .forWho-list__single {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
        padding-bottom: 11px; } }
    @media (max-width: 767px) {
      .forWho-list__single {
        padding-bottom: 0px; } }
    @media (max-width: 767px) {
      .forWho-list__single-text {
        line-height: 1.77; } }



