/* eof mixins */
.notification {
  background-color: #120d09;
  color: #fff;
  padding: 29px 0 27px 0; }
  @media (max-width: 1199px) {
    .notification {
      padding-bottom: 23px;
      padding-top: 26px; } }
  @media (max-width: 767px) {
    .notification {
      padding-bottom: 29px;
      padding-top: 22px; } }
  .notification-inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    position: relative; }
    @media (max-width: 767px) {
      .notification-inner {
        display: block; } }
  .notification__text {
    margin: 18px 0 0 0;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    max-width: 734px;
    padding: 0 0 4px 0; }
    @media (max-width: 767px) {
      .notification__text {
        display: block;
        max-width: 100%;
        font-size: 14px;
        line-height: 1.71;
        margin-bottom: 32px; } }
    .notification__text .svg-icon {
      margin: 0 29px 0 0;
      width: 71px;
      max-width: 71px;
      height: 28px;
      line-height: 28px;
      flex-shrink: 0; }
      @media (max-width: 991px) {
        .notification__text .svg-icon {
          margin-top: 20px;
          width: 64px;
          max-width: 64px;
          height: 25px;
          line-height: 25px;
          margin-right: 19px; } }
      @media (max-width: 767px) {
        .notification__text .svg-icon {
          margin-top: 0;
          margin-bottom: 22px; } }
  .notification__button {
    flex-shrink: 0;
    margin: 11px 40px 0 40px; }
    @media (max-width: 991px) {
      .notification__button {
        margin-left: 31px;
        margin-right: 31px; } }
    @media (max-width: 767px) {
      .notification__button {
        margin-left: 0px;
        margin-right: 0px; } }
  .notification__close {
    margin: 15px -5px 0 0;
    flex-shrink: 0; }
    @media (max-width: 767px) {
      .notification__close {
        position: absolute;
        top: 0;
        right: 23px;
        margin-top: -5px; } }
    @media (max-width: 767px) {
      .notification__close {
        right: 20px; } }
    .notification__close-inner {
      width: 34px;
      height: 34px;
      position: relative;
      display: block; }
      .notification__close-inner-line {
        height: 3px;
        width: 28px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
        background-color: #fff; }
        @media (max-width: 767px) {
          .notification__close-inner-line {
            height: 2px;
            width: 26px; } }
        .notification__close-inner-line:first-child {
          transform: translate(-50%, -50%) rotate(-45deg); }
      .notification__close-inner:hover .notification__close-inner-line, .notification__close-inner:active .notification__close-inner-line, .notification__close-inner:focus .notification__close-inner-line {
        background-color: #cccccc; }



