/* eof mixins */
.footer {
  background-color: #413d3a;
  color: #fff;
  margin: 30px 0 0 0; }
  @media (max-width: 767px) {
    .footer {
      margin-top: 15px; } }
  .footer.menuActive {
    transition: filter 0s 0.2s;
    filter: blur(2px); }
    @media (max-width: 991px) {
      .footer.menuActive {
        filter: blur(0px); } }
  .footer-top {
    padding: 36px 0 71px 0;
    border-bottom: 1px solid #6c6967; }
    @media (max-width: 991px) {
      .footer-top {
        padding-top: 34px;
        padding-bottom: 48px; } }
    @media (max-width: 767px) {
      .footer-top {
        padding-top: 20px;
        padding-bottom: 16px; } }
  .footer-bottom {
    font-size: 14px;
    line-height: 2.14;
    padding: 5px 0 16px 0; }
    @media (max-width: 991px) {
      .footer-bottom {
        font-size: 13px;
        line-height: 2.31;
        padding-top: 4px; } }
    @media (max-width: 767px) {
      .footer-bottom {
        font-size: 12px;
        line-height: 2;
        padding-top: 24px;
        padding-bottom: 24px; } }
    .footer-bottom-inner {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-wrap: wrap; }
      @media (max-width: 767px) {
        .footer-bottom-inner {
          display: block; } }
    @media (max-width: 767px) {
      .footer-bottom__single {
        margin-bottom: 24px; } }
    .footer-bottom__single:last-child {
      margin: 0 0 0 auto; }
      @media (max-width: 767px) {
        .footer-bottom__single:last-child {
          margin-bottom: 0px; } }
  .footer a {
    color: #fff;
    text-decoration: none; }
    .footer a:hover, .footer a:focus, .footer a:active {
      color: #cccccc;
      text-decoration: none; }
  .footer-menu {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: 0 -20px; }
    @media (max-width: 991px) {
      .footer-menu {
        margin-left: -24px;
        margin-right: -24px; } }
    .footer-menu__single {
      padding: 6px 20px; }
      @media (max-width: 991px) {
        .footer-menu__single {
          padding-left: 24px;
          padding-right: 24px; } }
      @media (max-width: 767px) {
        .footer-menu__single {
          padding-bottom: 10px;
          width: 50%; } }



