/* eof mixins */
.breadcrumbs {
  padding: 22px 0 0 0; }
  .breadcrumbs-list {
    font-size: 14px;
    line-height: 1.75;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap; }
    @media (max-width: 991px) {
      .breadcrumbs-list {
        font-size: 13px;
        line-height: 1.85; } }
    @media (max-width: 767px) {
      .breadcrumbs-list {
        font-size: 12px;
        line-height: 1.67; } }
    .breadcrumbs-list__single {
      display: inline-flex;
      align-items: center;
      justify-content: flex-start;
      color: #000;
      text-decoration: none;
      padding: 0 0 0 1px;
      margin: 0 10px 0 0; }
      .breadcrumbs-list__single .svg-icon {
        color: #7f7f7f;
        stroke: currentColor;
        transform: rotate(90deg);
        margin: 0 11px 0 0; }
        @media (max-width: 991px) {
          .breadcrumbs-list__single .svg-icon {
            margin-right: 13px;
            font-size: 15px; } }
        @media (max-width: 767px) {
          .breadcrumbs-list__single .svg-icon {
            margin-right: 8px;
            font-size: 15px; } }
      .breadcrumbs-list__single:hover, .breadcrumbs-list__single:focus, .breadcrumbs-list__single:active {
        color: #666666;
        text-decoration: none; }
        .breadcrumbs-list__single:hover .svg-icon, .breadcrumbs-list__single:focus .svg-icon, .breadcrumbs-list__single:active .svg-icon {
          stroke: #666666; }
  .breadcrumbs_padding {
    padding-top: 40px; }
    @media (max-width: 991px) {
      .breadcrumbs_padding {
        padding-top: 23px; } }
    @media (max-width: 767px) {
      .breadcrumbs_padding {
        padding-top: 22px; } }
  .breadcrumbs_backoffice {
    width: 100%;
    display: none;
    padding-top: 0;
    padding-bottom: 31px; }
    @media (max-width: 991px) {
      .breadcrumbs_backoffice {
        display: block; } }



